import { INotification } from "jupyterlab_toastify";
// TODO migrate INotifications -> Notification once jupyterlab is upgraded to 3.6.0+
// https://github.com/jupyterlab/jupyterlab/pull/12959
// import { Notification } from '@jupyterlab/apputils';
// This function is also used in data_catalog
// TODO move to shared library?
export async function copyToClipboard(text, successMessage = "Copied to clipboard", errorMessage = "Failed to copy to clipboard") {
    try {
        await navigator.clipboard.writeText(text);
        INotification.success(successMessage, {
            autoClose: 5000,
        });
    }
    catch (error) {
        INotification.error(errorMessage, {
            autoClose: 5000,
        });
        console.error(errorMessage, error);
    }
}
